import React from 'react'
import './index.css'
import Project from '../../Components/Project'

const projectInfo = [
  {
    title: 'Juke',
    desc: 'Juke was created to provide a user interface for streaming and downloading music via the BitTorrent protocol - iTunes for torrents, essentially. It was created using JS, Node, HTML and Vue, among others. The creation of Juke taught Nick many lessons around software architecture and technical debt.',
    src: '/jpeg/juke.jpg',
    link: 'https://github.com/sinclairnick/juke'
  },
  {
    title: 'ChemsultNZ',
    desc: 'ChemsultNZ solicited Nick to create a web app which allowed users to log in and see their order information. Initially Nick created this app using Express, Mongo and HTML Templating, but he later remade both the API and the UI to convert the site into a Single Page Application, with React.',
    src: '/jpeg/chemsult.jpg'
  },
  {
    title: 'Magnetic Music',
    desc: 'Magnetic Music is an npm package used in Juke which searches for and returns music torrent information including Artist(s), Song(s), Album name(s) etc. This package was necessitated by the creation of Juke.',
    src: '/jpeg/magnetic-music.jpg',
    link: 'https://www.npmjs.com/package/magnetic-music'
  },
  {
    title: 'WCM',
    desc: 'The Wellington City Mission website prototype was created using Bootstrap, HTML and CSS. It was one of my first projects and taught Nick the power of CSS, and how frameworks can augment and constrain the complexity of CSS.',
    src: '/jpeg/wcm.jpg'
  }
]

export default () => {
  return (
    <div className='Projects'>
    <h2>Projects</h2>
      {projectInfo.map((x, i) => <Project {...x} key={i} index={i}/>)}
    </div>
  )
}



