import { START_DRIP, END_DRIP } from '.';

export default (state = { name: '', dripping: false }, action) => {
  switch (action.type) {
    case START_DRIP:
      return action.payload || state;
    case END_DRIP:
      return { ...state, name: '', dripping: false};
    default:
      return state;
  }
}


