import React from 'react';
import startDrip from '../../actions/drip';
import { connect } from 'react-redux';

const Content = ({ name, logo, blurb, title, selected, dispatch }) => {
  const handleClick = (e) => {
    if (window.innerWidth <= 750) {
      const el = document.querySelector(`#${name}-logo`);
      startDrip(dispatch, name);
      setTimeout(() => el.scrollIntoView({
        behaviour: 'smooth',
        block: "start",
        inline: "nearest"
      }), 155);
    }

  }
  return (
    <div id={`${name}-logo`} className={`content ${selected ? 'selected' : ''}`}>
      <div className="logo" onClick={handleClick}>{logo}</div>
      <div className={`blurb`}>
        <div className="title">{title}</div>
        <div>{blurb}</div>
      </div>
    </div>
  )
}

export default connect()(Content)


