import React from 'react'
import './index.css'

export default ({ title, desc, src, index, link }) => {
  const style ={
    background: `url( ${src} )`,
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: '50% 50%',
    borderRadius: `
    ${Math.random()*100}%
    ${Math.random()*100}%
    ${Math.random()*100}%
    ${Math.random()*100}%`
  }
  const align = {
    justifySelf: link % 2 === 0 ? 'start' : 'end'
  }
  const rowD = {
    flexDirection: index % 2 === 0 ? 'row' : 'row-reverse'
  }
  return (
    <div className='Project' style={rowD}>
      <div className='left'>
        {link ? <a href={link}><h1>{title}</h1></a>
        : <h1>{title}</h1> 
        }
        <p>{desc}</p>
      </div>
      <div className="img-container" style={align}>
        <div className="img" style={style}>
        </div>
      </div>
    </div>
  )
}



