import { START_DRIP, END_DRIP } from '../reducers';

export default (dispatch, name) =>{
  // starts drip
  dispatch({
    type: START_DRIP,
    payload: {
      name,
      dripping: true,
      recent: name
    }
  })
  // ends drip after t_out ms
  const t_out = 500;
  setTimeout(() => dispatch({ type: END_DRIP }), t_out);
}


