import React, { Component } from 'react';
import { connect } from 'react-redux';
import jarInfo from '../Board/jarInfo';
import Content from '../Content';
import TubSide from '../TubSide';
import Fly from '../Fly';

class Info extends Component {
  render = () => {
    const { drip } = this.props;
    return (
      <div className={`info ${(drip && drip.recent) || ''}`}>
        <TubSide toClass="tub one"/>
        <TubSide toClass="tub two"/>
        <div className="info-overlay">
        <div className="fly one">
        <Fly />
        </div>
        <div className="fly two">
        <Fly />
        </div>
          {jarInfo.map((x, i) => <Content
            logo={x.logo}
            blurb={x.blurb}
            title={x.title}
            name={x.name}
            selected={drip.recent === x.name}
            key={i}
          />)}
        </div>
      </div >
    )
  }
}

const mapStateToProps = ({ drip }) => ({ drip });
export default connect(mapStateToProps)(Info);


